html, body {
  height: 100%; }

html {
  padding: 5px 5px 5px;
  box-sizing: border-box;
  font: 62.5% "PT Sans", Helvetica, Arial, sans-serif; }

body {
  background: #eee;
  background: url("/assets/background.jpg");
  font-family: "PT Sans", Helvetica, Arial, sans-serif;
  font-size: 1.6em;
  font-weight: 300;
  letter-spacing: .01em;
  line-height: 1.6; }
  @media (max-width: 900px) {
    body {
      font-size: 9pt; } }

* {
  margin: 0;
  padding: 0; }
  *, *:before, *:after {
    box-sizing: inherit; }

.body-wrap {
  padding-top: 0.7em;
  min-height: 100%;
  background: none; }

.hidden {
  display: none; }

.vishidden {
  opacity: 0; }

.links > a {
  padding-left: 3px;
  padding-right: 3px; }
  .links > a:first-child {
    padding-left: 0; }
  .links > a:last-child {
    padding-right: 0; }

p {
  margin-top: 0; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 300;
  margin-bottom: 2.0rem;
  margin-top: 0; }

h1 {
  font-size: 4.0rem;
  letter-spacing: -.1rem;
  line-height: 1.2; }

h2 {
  font-size: 3.6rem;
  letter-spacing: -.1rem;
  line-height: 1.25; }

h3 {
  font-size: 3.0rem;
  letter-spacing: -.1rem;
  line-height: 1.3; }

h4 {
  font-size: 2.4rem;
  letter-spacing: -.08rem;
  line-height: 1.35; }

h5 {
  font-size: 1.8rem;
  letter-spacing: -.05rem;
  line-height: 1.5; }

h6 {
  font-size: 1.6rem;
  letter-spacing: 0;
  line-height: 1.4; }

@media (min-width: 40rem) {
  h1 {
    font-size: 5.0rem; }
  h2 {
    font-size: 4.2rem; }
  h3 {
    font-size: 3.6rem; }
  h4 {
    font-size: 3.0rem; }
  h5 {
    font-size: 2.4rem; }
  h6 {
    font-size: 1.5rem; } }

#push-sidebar {
  position: fixed;
  top: 15px;
  left: 10px;
  overflow: hidden;
  cursor: pointer;
  z-index: 9000;
  color: #eee;
  font: 30px fontawesome;
  transition: transform .3s, left .1s, color .05s ease-in-out, text-shadow .3s linear; }
  @media (max-width: 600px) {
    #push-sidebar {
      color: #222; }
      #push-sidebar:before {
        content: 'Swipe'; } }
  #push-sidebar:before {
    content: '\f0c9'; }
  #push-sidebar:hover {
    text-shadow: 1px 1px 1px #bbb; }
  #push-sidebar.open {
    transform: translate3d(255px, 0, 0) rotate(360deg);
    text-shadow: 1px 1px 2px #666;
    color: #000; }
    #push-sidebar.open:hover {
      text-shadow: 2px 2px 1px #666; }
    #push-sidebar.open:before {
      content: '\f00d'; }

#scroll-to-top {
  position: fixed;
  z-index: 9000;
  font-size: 40px;
  bottom: 30px;
  right: 10px;
  height: 10px;
  font: 40px fontawesome;
  color: #bbb;
  transition: color .3s linear, text-shadow .3s; }
  #scroll-to-top:hover {
    color: #fff;
    text-shadow: 1px 1px 1px #aaa; }
  #scroll-to-top:before {
    content: '\f106'; }
  @media (max-width: 600px) {
    #scroll-to-top {
      color: #333; }
      #scroll-to-top:hover {
        color: #111; } }

#github,
#instagram,
#email,
#twitter,
#pocket,
#slack,
#star {
  font: 25px fontawesome;
  color: black;
  padding-left: 7px;
  padding-right: 7px;
  text-decoration: none;
  transition: text-shadow .3s; }
  #github:hover,
  #instagram:hover,
  #email:hover,
  #twitter:hover,
  #pocket:hover,
  #slack:hover,
  #star:hover {
    text-shadow: 1px 1px 2px #666; }
  #github:first-child,
  #instagram:first-child,
  #email:first-child,
  #twitter:first-child,
  #pocket:first-child,
  #slack:first-child,
  #star:first-child {
    padding-left: 0; }
  #github:last-child,
  #instagram:last-child,
  #email:last-child,
  #twitter:last-child,
  #pocket:last-child,
  #slack:last-child,
  #star:last-child {
    padding-right: 0; }

#github:before {
  content: 'GitHub';
  content: '\f09b'; }

#instagram:before {
  content: 'Instagram';
  content: '\f16d'; }

#email:before {
  content: 'E-mail';
  content: '\f0e0'; }

#twitter:before {
  content: 'Twitter';
  content: '\f099'; }

#pocket:before {
  content: 'Pocket';
  content: '\f265'; }

#slack:before {
  content: 'Slack';
  content: '\f198'; }

#star:before {
  content: 'Star';
  content: '\2605'; }

#up:before {
  content: 'Up';
  content: '\f106'; }

.sidebar {
  position: fixed;
  background: #fff;
  height: 100%;
  z-index: 6000;
  flex: 1;
  width: 300px;
  left: 0;
  transform: translate3d(-300px, 0, 0);
  top: 0;
  padding-top: 15px;
  padding-left: 20px;
  overflow: hidden;
  transition: transform .3s, box-shadow .5s;
  /*@media (max-width: 540px)
    padding-top: 15px*/ }
  .sidebar.open {
    transform: translate3d(0, 0, 0);
    box-shadow: 25px 25px 50px 0 rgba(0, 0, 0, 0.75); }

.links {
  position: fixed;
  top: 90%; }

.content {
  color: black;
  z-index: 1;
  height: 100%;
  flex: 1;
  transform: translateX(0);
  transition: transform .3s; }
  .content.open {
    transform: translateX(200px); }

.page-wrap {
  margin: 0 auto;
  padding: 10px 10px 10px;
  box-shadow: 0 0 40px 15px rgba(0, 0, 0, 0.75);
  height: 100%;
  width: 80%;
  background: #fff;
  right: 0;
  left: 0;
  color: #333;
  font: 1.5rem "PT Sans", Helvetica, Arial, sans-serif;
  hypens: auto; }
  @media (max-width: 600px) {
    .page-wrap {
      padding: 0 10px 10px;
      width: 100%;
      font-size: 1.5rem; } }

code, .code {
  hypens: auto;
  white-space: pre-wrap;
  display: block;
  background: rgba(243, 243, 243, 0.5);
  color: #666;
  padding: 0.5rem 1rem;
  margin: 1.5rem 1.5rem;
  line-height: 140%;
  border-left: .2rem solid #9b4dca;
  font: 95% Iosevka, Courier, monospace;
  hypens: auto;
  /*@media (max-width: 900px)
    font: 0.8em $code*/ }

.navigation {
  margin: 0 auto;
  width: 70%;
  height: 40px;
  text-align: center;
  z-index: 9000;
  position: fixed;
  bottom: 0;
  background: #fff;
  color: #000;
  left: 0;
  right: 0;
  font: 13pt "PT Sans", Helvetica, Arial, sans-serif;
  box-shadow: 0 0 10px #000; }
  .navigation a {
    padding: 4px 4px; }
  .navigation a:first-child {
    padding-left: 0; }
  .navigation a:last-child {
    padding-right: 0; }
